@tailwind base;
@tailwind components;
@tailwind utilities;

/* Mobile fixes for About Me section */
@media screen and (max-width: 768px) {
  #about {
    display: block !important;
    visibility: visible !important;
    opacity: 1 !important;
    min-height: 100vh !important;
    position: relative !important;
    z-index: 1 !important;
    overflow: visible !important;
    transform: none !important;
  }
  
  /* Fix motion animations on mobile */
  #about .motion-div,
  #about motion.div,
  #about .stat-card {
    opacity: 1 !important;
    transform: none !important;
    animation: none !important;
    visibility: visible !important;
  }
  
  /* Fix nested containers */
  #about > div,
  #about > div > div {
    display: block !important;
    visibility: visible !important;
  }
} 