@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Completely disable transitions for typing animation components */
.typing-text, 
.typing-container, 
.typing-line,
.typing-cursor-permanent,
.typing-text span,
.no-theme-transition,
.theme-transition-exempt,
h1 .flex,
h1 [class*="text-"] {
  transition: none !important;
  animation-delay: 0s !important;
  transition-property: none !important;
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -o-transition: none !important;
  will-change: auto !important;
}

/* Ensure all child elements of these containers have no transitions */
.no-theme-transition *,
.theme-transition-exempt *,
.typing-container * {
  transition: none !important;
  transition-property: none !important;
  -webkit-transition: none !important; 
  -moz-transition: none !important;
  -o-transition: none !important;
}

/* Additional classes to prevent theme transition effects */
.no-theme-transition,
.theme-transition-exempt {
  transition: none !important;
  animation-delay: 0s !important;
}

/* Override color transition only, keeping other animations */
.typing-text.theme-transition-exempt,
.typing-cursor-permanent {
  transition: none !important;
  color: inherit;
}

.light {
  --color-primary: #ffffff;
  --color-secondary: #1f2937;
  --color-text: #1f2937;
  --color-background: #ffffff;
  --color-card: #f3f4f6;
  --color-card-border: #e5e7eb;
}

.dark {
  --color-primary: #050816;
  --color-secondary: #aaa6c3;
  --color-text: #ffffff;
  --color-background: #050816;
  --color-card: #1f2937;
  --color-card-border: #374151;
  color-scheme: dark;
}

.violet-gradient {
  background: #804dee;
  background: linear-gradient(-90deg, #804dee 0%, rgba(60, 51, 80, 0) 100%);
  background: -webkit-linear-gradient(
    -90deg,
    #804dee 0%,
    rgba(60, 51, 80, 0) 100%
  );
}

.bg-primary {
  background-color: var(--color-background);
}

.text-primary {
  color: var(--color-text);
}

.text-secondary {
  color: var(--color-secondary);
}

.border-secondary {
  border-color: var(--color-secondary);
}

.bg-secondary {
  background-color: var(--color-secondary);
}

/* CountUp Animation */
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

.stat-card {
  transition: all 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.text-shadow-glow {
  text-shadow: 0 0 4px rgba(168, 85, 247, 0.4);
  transition: text-shadow 0.2s ease-in-out;
}

.number-glow {
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  background-image: linear-gradient(to right, #a855f7, #ec4899);
  position: relative;
  z-index: 1;
}

.number-glow::after {
  content: attr(data-content);
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  background-image: linear-gradient(to right, #a855f7, #ec4899);
  filter: blur(4px) brightness(1.3);
  opacity: 0.7;
}

.number-container {
  position: relative;
  display: inline-block;
  overflow: visible;
}

/* Improved Scroll Behavior */
html {
  scroll-behavior: smooth;
}

/* Enhanced About Me section highlight on scroll */
@keyframes fadeInSection {
  from {
    opacity: 0.4;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleUpSection {
  0% {
    opacity: 0.6;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

#about.in-viewport {
  animation: scaleUpSection 1.8s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
  transform-origin: center;
  will-change: transform, opacity;
  backface-visibility: hidden;
}

/* Keep scroll-progress class for the progress bar at the top */
.scroll-progress {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(to right, rgba(168, 85, 247, 0.7), rgba(236, 72, 153, 0.7));
  transform-origin: 0 50%;
  transform: scaleX(0);
  z-index: 1000;
}

/* Adjust for fixed navbar */
#about, #skills {
  scroll-margin-top: 80px; /* Height of your navbar plus some padding */
}

@media (max-width: 768px) {
  #about, #skills {
    scroll-margin-top: 70px; /* Slightly less for mobile */
  }
}

/* Mobile responsiveness fixes */
@media (max-width: 768px) {
  #about {
    scroll-margin-top: 60px; /* Adjusts for fixed navbar on mobile */
    overflow-x: hidden;
    display: block !important; /* Force display on mobile */
    visibility: visible !important; /* Ensure visibility */
    opacity: 1 !important; /* Ensure opacity is set */
    height: auto !important; /* Prevent height restrictions */
    min-height: 100% !important; /* Ensure minimum height */
    position: relative !important; /* Reset any absolute positioning */
    z-index: 1 !important; /* Ensure proper stacking context */
    transform: none !important; /* Remove any transforms that might hide it */
  }
  
  .number-glow {
    font-size: 1.75rem; /* Smaller font size for stats on mobile */
  }
  
  /* Ensure content doesn't overflow on small screens */
  .max-w-7xl {
    width: 100%;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    overflow: visible !important; /* Prevent overflow issues */
  }
  
  /* Improve readability of text on mobile */
  #about p {
    font-size: 0.95rem;
    line-height: 1.6;
  }
  
  /* Make sure skill cards are readable */
  #skills .text-sm {
    line-height: 1.5;
  }
  
  /* Fix any potential animation issues for mobile */
  #about .motion-div {
    opacity: 1 !important;
    transform: none !important;
  }
}

/* Green glow animation for buttons */
@keyframes greenGlowPulse {
  0% {
    box-shadow: 0 0 0 0 rgba(34, 197, 94, 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(34, 197, 94, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(34, 197, 94, 0);
  }
}

/* Typing Animation and Cursor Effects - Enhanced */
.typing-container {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
}

.typing-line {
  display: block;
  font-size: 2.75rem; /* Base font size for desktop */
  line-height: 1.2;
  font-weight: 700;
  margin-bottom: 0.25rem;
  white-space: nowrap;
  overflow: visible;
}

.typing-text {
  display: inline-block;
  white-space: pre;
  position: relative;
}

/* Original cursor that fades out */
.typing-cursor {
  display: inline-block;
  width: 3px;
  height: 1.1em;
  background-color: #a855f7;
  margin-left: 0.1em;
  vertical-align: text-bottom;
  animation: blink-caret 0.75s step-end infinite;
  position: relative;
  top: 0.12em;
  border-radius: 1px;
  box-shadow: 0 0 8px rgba(168, 85, 247, 0.6);
}

/* Permanent blinking cursor */
.typing-cursor-permanent {
  display: inline-block;
  width: 3px;
  height: 1em;
  background: currentColor;
  margin-left: 2px;
  animation: blink 1s infinite;
  vertical-align: text-top;
}

@keyframes blink {
  50% { opacity: 0; }
}

/* Responsive adjustments */
@media (max-width: 1024px) {
  .typing-line {
    font-size: 2.5rem;
  }
}

@media (max-width: 768px) {
  .typing-container {
    max-width: 100%;
    padding: 0 1rem;
  }
  
  .typing-line {
    font-size: 2rem;
    white-space: normal; /* Allow text to wrap on mobile */
    word-wrap: break-word;
    hyphens: auto;
  }
}

@media (max-width: 480px) {
  .typing-line {
    font-size: 1.75rem;
    line-height: 1.3;
    margin-bottom: 0.5rem;
  }
  
  /* Adjust spacing for the "by BM SAKIF" line */
  .typing-line:last-child {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 0.5rem;
  }
  
  /* Ensure gradient text remains visible */
  .typing-line .bg-gradient-to-r {
    display: inline-block;
    padding: 0.25rem 0;
  }
}

/* Ensure text remains visible during theme transitions */
.typing-text {
  opacity: 1 !important;
  visibility: visible !important;
  display: inline-block !important;
}

.green-glow-button {
  position: relative;
  animation: greenGlowPulse 2s infinite;
}

.green-text {
  color: #22c55e;
  font-weight: 700;
}

@media (max-width: 768px) {
  .typing-container {
    min-height: 4em;
    margin-bottom: 3rem;
  }
} 